import { NextResponse } from 'next/server';
import pool from '../../lib/db'; 

// --- GET: ‡∏î‡∏∂‡∏á‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡∏™‡∏£‡∏∏‡∏õ‡∏ö‡∏±‡∏ç‡∏ä‡∏µ (‡∏£‡∏ß‡∏°‡∏ó‡∏∏‡∏Å‡∏≠‡∏¢‡πà‡∏≤‡∏á: ‡∏Ç‡∏≤‡∏¢ + ‡∏ã‡∏∑‡πâ‡∏≠ + ‡∏à‡πà‡∏≤‡∏¢‡∏ó‡∏±‡πà‡∏ß‡πÑ‡∏õ) ---
export async function GET() {
  try {
    // ------------------------------------------------------------------
    // 1. ‡∏Ñ‡∏≥‡∏ô‡∏ß‡∏ì‡∏£‡∏≤‡∏¢‡∏£‡∏±‡∏ö (Income)
    // ------------------------------------------------------------------
    
    // 1.1 ‡∏¢‡∏≠‡∏î‡∏Ç‡∏≤‡∏¢‡∏à‡∏≤‡∏Å Orders (‡∏ñ‡πâ‡∏≤‡∏°‡∏µ‡∏ï‡∏≤‡∏£‡∏≤‡∏á orders)
    // *‡∏´‡∏°‡∏≤‡∏¢‡πÄ‡∏´‡∏ï‡∏∏: ‡∏ñ‡πâ‡∏≤‡∏¢‡∏±‡∏á‡πÑ‡∏°‡πà‡πÑ‡∏î‡πâ‡∏ó‡∏≥‡∏ï‡∏≤‡∏£‡∏≤‡∏á orders ‡πÉ‡∏´‡πâ‡∏Ñ‡∏≠‡∏°‡πÄ‡∏°‡∏ô‡∏ï‡πå‡∏ö‡∏£‡∏£‡∏ó‡∏±‡∏î‡∏ô‡∏µ‡πâ‡πÅ‡∏•‡πâ‡∏ß‡πÉ‡∏´‡πâ totalSales = 0
    let totalSales = 0;
    try {
        const [salesResult] = await pool.query("SELECT SUM(total_amount) as total FROM orders");
        totalSales = Number(salesResult[0].total || 0);
    } catch (e) {
        // ‡∏î‡∏±‡∏Å Error ‡πÑ‡∏ß‡πâ‡∏Å‡πà‡∏≠‡∏ô ‡πÄ‡∏ú‡∏∑‡πà‡∏≠‡∏¢‡∏±‡∏á‡πÑ‡∏°‡πà‡∏°‡∏µ‡∏ï‡∏≤‡∏£‡∏≤‡∏á orders
        console.log("‚ö†Ô∏è ‡∏¢‡∏±‡∏á‡πÑ‡∏°‡πà‡∏°‡∏µ‡∏ï‡∏≤‡∏£‡∏≤‡∏á Orders ‡∏Ç‡πâ‡∏≤‡∏°‡∏Å‡∏≤‡∏£‡∏Ñ‡∏≥‡∏ô‡∏ß‡∏ì‡∏™‡πà‡∏ß‡∏ô‡∏ô‡∏µ‡πâ");
    }

    // 1.2 ‡∏£‡∏≤‡∏¢‡∏£‡∏±‡∏ö‡∏≠‡∏∑‡πà‡∏ô‡πÜ ‡∏à‡∏≤‡∏Å Transactions (Manual Input)
    const [otherIncomeResult] = await pool.query("SELECT SUM(amount) as total FROM transactions WHERE type = 'Income'");
    const totalOtherIncome = Number(otherIncomeResult[0].total || 0);

    const totalIncome = totalSales + totalOtherIncome;

    // ------------------------------------------------------------------
    // 2. ‡∏Ñ‡∏≥‡∏ô‡∏ß‡∏ì‡∏£‡∏≤‡∏¢‡∏à‡πà‡∏≤‡∏¢ (Expenses)
    // ------------------------------------------------------------------

    // 2.1 ‡∏ï‡πâ‡∏ô‡∏ó‡∏∏‡∏ô‡∏™‡∏¥‡∏ô‡∏Ñ‡πâ‡∏≤‡∏à‡∏≤‡∏Å PO (Purchase Orders) - ‡πÄ‡∏â‡∏û‡∏≤‡∏∞‡∏ó‡∏µ‡πà‡πÑ‡∏°‡πà‡∏¢‡∏Å‡πÄ‡∏•‡∏¥‡∏Å
    const [poExpenseResult] = await pool.query("SELECT SUM(total_amount) as total FROM purchase_orders WHERE status != 'Cancelled'");
    const totalPOExpense = Number(poExpenseResult[0].total || 0);

    // 2.2 ‡∏£‡∏≤‡∏¢‡∏à‡πà‡∏≤‡∏¢‡∏ó‡∏±‡πà‡∏ß‡πÑ‡∏õ‡∏à‡∏≤‡∏Å Transactions (Manual Input)
    const [manualExpenseResult] = await pool.query("SELECT SUM(amount) as total FROM transactions WHERE type = 'Expense'");
    const totalManualExpense = Number(manualExpenseResult[0].total || 0);

    const totalExpenses = totalPOExpense + totalManualExpense;

    // ------------------------------------------------------------------
    // 3. ‡∏Å‡∏≥‡πÑ‡∏£‡∏™‡∏∏‡∏ó‡∏ò‡∏¥
    // ------------------------------------------------------------------
    const netProfit = totalIncome - totalExpenses;

    // ------------------------------------------------------------------
    // 4. ‡∏î‡∏∂‡∏á‡∏£‡∏≤‡∏¢‡∏Å‡∏≤‡∏£‡πÄ‡∏Ñ‡∏•‡∏∑‡πà‡∏≠‡∏ô‡πÑ‡∏´‡∏ß‡∏•‡πà‡∏≤‡∏™‡∏∏‡∏î (‡∏£‡∏ß‡∏° 3 ‡∏ó‡∏´‡∏≤‡∏£‡πÄ‡∏™‡∏∑‡∏≠: Orders + PO + Manual)
    // ------------------------------------------------------------------
    
    // ‡πÉ‡∏ä‡πâ UNION ALL ‡πÄ‡∏û‡∏∑‡πà‡∏≠‡∏£‡∏ß‡∏°‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡∏à‡∏≤‡∏Å 3 ‡∏ï‡∏≤‡∏£‡∏≤‡∏á ‡πÅ‡∏•‡πâ‡∏ß‡πÄ‡∏£‡∏µ‡∏¢‡∏á‡∏ï‡∏≤‡∏°‡∏ß‡∏±‡∏ô‡∏ó‡∏µ‡πà
    const sqlRecent = `
      /* ‡∏™‡πà‡∏ß‡∏ô‡∏ó‡∏µ‡πà 1: ‡∏£‡∏≤‡∏¢‡∏Å‡∏≤‡∏£‡∏ã‡∏∑‡πâ‡∏≠‡∏Ç‡∏≠‡∏á (PO) */
      SELECT 
        CONCAT('PO-', id) as id,    -- ‡∏™‡∏£‡πâ‡∏≤‡∏á ID ‡∏õ‡∏•‡∏≠‡∏°‡πÑ‡∏°‡πà‡πÉ‡∏´‡πâ‡∏ã‡πâ‡∏≥ ‡πÄ‡∏ä‡πà‡∏ô PO-1
        po_number as ref_number,
        'Expense' as type, 
        '‡∏à‡∏±‡∏î‡∏ã‡∏∑‡πâ‡∏≠‡∏™‡∏¥‡∏ô‡∏Ñ‡πâ‡∏≤' as category, 
        total_amount as amount, 
        CONCAT('‡∏™‡∏±‡πà‡∏á‡∏ã‡∏∑‡πâ‡∏≠‡∏à‡∏≤‡∏Å ', (SELECT name FROM suppliers WHERE id = purchase_orders.supplier_id)) as description, 
        order_date as date 
      FROM purchase_orders 
      WHERE status != 'Cancelled'

      UNION ALL

      /* ‡∏™‡πà‡∏ß‡∏ô‡∏ó‡∏µ‡πà 2: ‡∏£‡∏≤‡∏¢‡∏Å‡∏≤‡∏£‡∏ó‡∏±‡πà‡∏ß‡πÑ‡∏õ (Transactions) */
      SELECT 
        CONCAT('TX-', id) as id,    -- ‡∏™‡∏£‡πâ‡∏≤‡∏á ID ‡∏õ‡∏•‡∏≠‡∏° ‡πÄ‡∏ä‡πà‡∏ô TX-1
        '-' as ref_number,
        type, 
        category, 
        amount, 
        description, 
        transaction_date as date 
      FROM transactions

      /* ‡∏™‡πà‡∏ß‡∏ô‡∏ó‡∏µ‡πà 3: ‡∏£‡∏≤‡∏¢‡∏Å‡∏≤‡∏£‡∏Ç‡∏≤‡∏¢ (Orders) - ‡∏ñ‡πâ‡∏≤‡∏°‡∏µ */
      /* ‡∏ñ‡πâ‡∏≤‡∏¢‡∏±‡∏á‡πÑ‡∏°‡πà‡∏°‡∏µ‡∏ï‡∏≤‡∏£‡∏≤‡∏á orders ‡πÉ‡∏´‡πâ‡∏•‡∏ö UNION ‡∏™‡πà‡∏ß‡∏ô‡∏ô‡∏µ‡πâ‡∏≠‡∏≠‡∏Å‡πÑ‡∏î‡πâ‡∏Ñ‡∏£‡∏±‡∏ö */
      UNION ALL
      SELECT 
        CONCAT('ORD-', id) as id, 
        CAST(id AS CHAR) as ref_number,
        'Income' as type, 
        '‡∏¢‡∏≠‡∏î‡∏Ç‡∏≤‡∏¢‡∏™‡∏¥‡∏ô‡∏Ñ‡πâ‡∏≤' as category, 
        total_amount as amount, 
        CONCAT('Order ‡∏Ç‡∏≤‡∏¢‡∏™‡∏¥‡∏ô‡∏Ñ‡πâ‡∏≤‡πÉ‡∏´‡πâ ', customer_name) as description, 
        sale_date as date 
      FROM orders

      ORDER BY date DESC 
      LIMIT 100
    `;

    // *‡∏´‡∏°‡∏≤‡∏¢‡πÄ‡∏´‡∏ï‡∏∏: ‡∏ñ‡πâ‡∏≤‡∏¢‡∏±‡∏á‡πÑ‡∏°‡πà‡∏°‡∏µ‡∏ï‡∏≤‡∏£‡∏≤‡∏á orders ‡πÉ‡∏´‡πâ‡πÄ‡∏≠‡∏≤ SQL ‡∏™‡πà‡∏ß‡∏ô Orders ‡∏≠‡∏≠‡∏Å ‡πÑ‡∏°‡πà‡∏á‡∏±‡πâ‡∏ô‡∏à‡∏∞ Error
    let recentTransactions = [];
    try {
        const [result] = await pool.query(sqlRecent);
        recentTransactions = result;
    } catch (e) {
        // Fallback: ‡∏ñ‡πâ‡∏≤ Query ‡∏£‡∏ß‡∏°‡∏û‡∏±‡∏á (‡πÄ‡∏ä‡πà‡∏ô‡πÑ‡∏°‡πà‡∏°‡∏µ‡∏ï‡∏≤‡∏£‡∏≤‡∏á Order) ‡πÉ‡∏´‡πâ‡∏î‡∏∂‡∏á‡πÅ‡∏Ñ‡πà PO ‡∏Å‡∏±‡∏ö Transaction
        console.log("‚ö†Ô∏è Query ‡∏£‡∏ß‡∏°‡∏°‡∏µ‡∏õ‡∏±‡∏ç‡∏´‡∏≤ (‡∏≠‡∏≤‡∏à‡∏à‡∏∞‡πÑ‡∏°‡πà‡∏°‡∏µ‡∏ï‡∏≤‡∏£‡∏≤‡∏á orders) -> ‡πÉ‡∏ä‡πâ‡πÇ‡∏´‡∏°‡∏î‡∏™‡∏≥‡∏£‡∏≠‡∏á");
        const sqlBackup = `
            SELECT CONCAT('PO-', id) as id, po_number as ref_number, 'Expense' as type, '‡∏à‡∏±‡∏î‡∏ã‡∏∑‡πâ‡∏≠‡∏™‡∏¥‡∏ô‡∏Ñ‡πâ‡∏≤' as category, total_amount as amount, CONCAT('‡∏™‡∏±‡πà‡∏á‡∏ã‡∏∑‡πâ‡∏≠‡∏à‡∏≤‡∏Å Supplier ID:', supplier_id) as description, order_date as date FROM purchase_orders WHERE status != 'Cancelled'
            UNION ALL
            SELECT CONCAT('TX-', id) as id, '-' as ref_number, type, category, amount, description, transaction_date as date FROM transactions
            ORDER BY date DESC LIMIT 100
        `;
        const [resultBackup] = await pool.query(sqlBackup);
        recentTransactions = resultBackup;
    }

    return NextResponse.json({
      summary: {
        income: totalIncome,
        expenses: totalExpenses,
        profit: netProfit
      },
      transactions: recentTransactions
    });

  } catch (error) {
    console.error("Accounting API Error:", error);
    return NextResponse.json({ error: error.message }, { status: 500 });
  }
}

// --- POST: ‡∏ö‡∏±‡∏ô‡∏ó‡∏∂‡∏Å‡∏£‡∏≤‡∏¢‡∏£‡∏±‡∏ö/‡∏£‡∏≤‡∏¢‡∏à‡πà‡∏≤‡∏¢ ‡πÄ‡∏û‡∏¥‡πà‡∏°‡πÄ‡∏ï‡∏¥‡∏° (Manual Entry) ---
export async function POST(request) {
  try {
    const body = await request.json();
    const { type, category, amount, description } = body;

    if (!amount || !category) {
      return NextResponse.json({ error: '‡∏Å‡∏£‡∏∏‡∏ì‡∏≤‡∏Å‡∏£‡∏≠‡∏Å‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡∏´‡∏°‡∏ß‡∏î‡∏´‡∏°‡∏π‡πà‡πÅ‡∏•‡∏∞‡∏à‡∏≥‡∏ô‡∏ß‡∏ô‡πÄ‡∏á‡∏¥‡∏ô' }, { status: 400 });
    }

    // ‡πÉ‡∏ä‡πâ NOW() ‡πÄ‡∏û‡∏∑‡πà‡∏≠‡∏ö‡∏±‡∏ô‡∏ó‡∏∂‡∏Å‡πÄ‡∏ß‡∏•‡∏≤‡∏õ‡∏±‡∏à‡∏à‡∏∏‡∏ö‡∏±‡∏ô‡∏•‡∏á transaction_date
    const sql = `INSERT INTO transactions (type, category, amount, description, transaction_date) VALUES (?, ?, ?, ?, NOW())`;
    await pool.query(sql, [type, category, amount, description]);

    return NextResponse.json({ success: true, message: '‡∏ö‡∏±‡∏ô‡∏ó‡∏∂‡∏Å‡∏£‡∏≤‡∏¢‡∏Å‡∏≤‡∏£‡∏™‡∏≥‡πÄ‡∏£‡πá‡∏à' });

  } catch (error) {
    return NextResponse.json({ error: error.message }, { status: 500 });
  }
}